  .body {
    background: black;
    font-family: "Oswald", sans-serif;
    font-style: italic;
    font-size: 9vw;
  }
  
  .subtitle {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
    font-size: 1vw;
    color: rgba(231, 216, 216, 0.4);
    text-transform: uppercase;
    letter-spacing: 1em;
    text-align: right;
    position: absolute;
    left: 60%;
    animation: glitch-2 5s 5.02s infinite;
  }
  
  .glitch, .glow {
    color: #dfbfbf;
    position: relative;
    
    animation: glitch 10s 10s infinite;
  }
  
  .glitch::before, .glow::before {
    content: attr(data-text);
    position: absolute;
    left: -2px;
    text-shadow: -5px 0 magenta;
    background: black;
    overflow: hidden;
    top: 0;
    animation: noise-1 3s linear infinite alternate-reverse, glitch 10s 10s infinite;
  }
  
  .glitch::after, .glow::after {
    content: attr(data-text);
    position: absolute;
    left: 2px;
    text-shadow: -5px 0 lightgreen;
    background: black;
    overflow: hidden;
    top: 0;
    animation: noise-2 2s linear infinite alternate-reverse, glitch 10s 10s infinite;
  }
  
  @keyframes glitch {
    1% {
      transform: rotateX(10deg) skewX(90deg);
    }
    2% {
      transform: rotateX(0deg) skewX(0deg);
    }
  }
